import React from 'react';
import PropTypes from 'prop-types';

const Icon = ({ name, ...restProps }) =>
    <svg {...restProps}>
        <use
            xmlnsXlink="http://www.w3.org/1999/xlink"
            xlinkHref={`/icons/sprites.svg#${name}`}
        />
    </svg>;

Icon.propTypes = {
    name: PropTypes.string.isRequired,
};

export default Icon;



